<template>
  <div>
    <div class="main list-container contents">
      <h1 class="page-header-title">부동산 실거래</h1>
      <check-box
        class="page-header"
        v-on:childEvent="searchCityData"
      ></check-box>
      <LoadingSpinner v-if="isLoading"></LoadingSpinner>
      <ul v-else>
        <PostListItem
          v-for="postItem in postItems"
          :key="postItem.id"
          :postItem="postItem"
          @refresh="fetchData"
        ></PostListItem>
      </ul>
    </div>
    <router-link to="/add" class="create-button">
      <i class="ion-md-add"></i>
    </router-link>

    <div class="overflow-auto">
      <div class="mt-3">
        <b-pagination
          v-model="page"
          :per-page="pagerows"
          :total-rows="rows"
          @page-click="fetchData"
          size="sm"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import checkBox from '@/components/checkBox.vue';
import PostListItem from '@/components/posts/PostListItem.vue';
import LoadingSpinner from '@/components/common/LoadingSpinner.vue';
import { fetchAPT } from '@/api/posts';

export default {
  components: {
    PostListItem,
    LoadingSpinner,
    checkBox,
  },
  data() {
    return {
      page: 1,
      pagerows: 20,
      rows: 100,
      postItems: [],
      isLoading: false,
      cityL: '',
      cityM: '',
      cityS: '',
    };
  },
  methods: {
    async fetchData() {
      this.isLoading = true;
      const aptData = {
        cityL: this.cityL,
        cityM: this.cityM,
        cityS: this.cityS,
        page: this.page,
      };
      const { data } = await fetchAPT(aptData);
      console.log(data);
      console.log('*-*-*-*-*-');
      this.isLoading = false;
      this.postItems = data.content;
      this.rows = data.totalElements;
    },
    searchCityData(aptData) {
      console.log('씽크');
      this.cityL = aptData.cityL;
      this.cityM = aptData.cityM;
      this.cityS = aptData.cityS;
      this.fetchData();
    },
  },
  created() {
    console.log('creates');
    this.fetchData();
  },
};
</script>
